.welcome .wrapper{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 15% 0;
  .img-right{
    display: flex;
    align-self: center;
    justify-content: center;
    img{
      max-width: 50%;
      height: 100%;
      display: block;
      border: 10px;
      border-radius: 15px;
  }
}
}
//////////////////////////////////////////////
.discover{
  display: grid;
  grid-template-rows: 2, 1fr;
  margin: 0 auto;
  background-image: url(../img/bg-intro-01.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment : fixed;
  .txt-center{
    padding: 150px 0;
    text-align: center;
  }
  h4{
    color: white;
  }
}

////////////////////////////////////////

.blog-3col .wrapper{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin: 10% auto;
  .col{
    display: flex;
    flex-direction: column;
    img{
      display: block;
      width: 100%;
      border-radius: 15px;
      transition: all 0.5s ease-in-out;
      &:hover{
        transform: scale(1.1);
      }
    }
    h5{
      margin-bottom: 3%;
    }
  }
}

/////////////////////////////////////////
.menu .wrapper{
  display: grid;
  padding-bottom: 10%;
  .txt-center{
    padding: 50px 0;
    text-align: center;
  }
  .menu-links{
    display: grid;
    grid-template-areas: "lunch diner ."
                        "lunch diner ."
                        "happy happy .";
    grid-auto-rows: calc(200px + 1vw);
    gap: 2rem;
    div{
      max-width: 100%;
      border-radius: 15px;
      transition: all 0.5s ease-in-out;
      background-position: center;
      background-size: cover;
      &:hover{
        transform: scale(1.1);
      }
    }
  div:nth-of-type(1){
    background-image: url(../img/our-menu-01.jpg);
    grid-area: lunch;
  }
  div:nth-of-type(2){
    background-image: url(../img/our-menu-05.jpg);
    grid-area: diner;
  }
  div:nth-of-type(3){
    background-image: url(../img/our-menu-08.jpg);
  }
  div:nth-of-type(4){
    background-image: url(../img/our-menu-10.jpg);
  }
  div:nth-of-type(5){
    background-image: url(../img/our-menu-13.jpg);
    grid-area: happy;
  }
  div:nth-of-type(6){
    background-image: url(../img/our-menu-16.jpg);
  }

  }
  
}